<script>
import axios from 'axios';
export default {
    name: 'GamerDetail',
    data() {
        return {
            gamerData : {}
        }
    },
    created() {
        this.getGamerData( this.$route.params.pid );
        
    },
    methods: {
        getGamerData( pid ){
            axios
                .get( 'https://thedivisiontab.com/api/player.php?pid=' + pid )
                .then( response => this.gamerData = response.data );
        }
    }
}
</script>